
@component-namespace page {
@component popup {
@descendent wrapper {
      padding: 0 20px;
      position: absolute 50% * * *;
      width: 100%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
button:not(:last-child) {
        margin-bottom: 20px;
}
}
.mint-popup-1 {
      width: 200px;
      border-radius: 8px;
      padding: 10px;
      -webkit-transform: translate(-50%, 0);
              transform: translate(-50%, 0);
h1 {
        font-size: 20px;
        color: #26a2ff;
}
p {
        margin-bottom: 10px;
}
}
.mint-popup-1::before {
      triangle: 10px top #fff;
      content: '';
      position: absolute;
      top: -20px;
      right: 50px;
}
.mint-popup-2 {
      width: 100%;
      height: 50px;
      text-align: center;
      background-color: rgba(0,0,0,.7);
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
}
.mint-popup-2 p {
      line-height: 50px;
      color: #fff;
}
.mint-popup-3 {
      width: 100%;
      height: 100%;
      background-color: #fff;
}
.mint-popup-3 .mint-button {
      position: absolute;
      width: 90%;
      top: 50%;
      left: 5%;
      -webkit-transform: translateY(-50%);
              transform: translateY(-50%);
}
.mint-popup-4 {
      width: 100%;
.picker-slot-wrapper, .picker-item {
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
}
}
}
}



